<script>
export default {
  name: 'Gauzi',
  data () {
    return {
      active_yi: -1,
      title: '',
      zi: '',
      object: {},
      yi_list:['', '', '', ''],
      ziyi: false,
      bb_yin: '',
    }
  },
  methods:{
    focusInput(){
       
    },
    copy_text(id) {
       var env = this;
       var clipboard = new this.clipboard('#'+id);
       clipboard.on('success', function () {
         clipboard.destroy()
       });
    },
    search(e){
        var env = this
        this.active_yi = -1
        this.$http.get('/open/zi/'+this.zi).then(function(resp){
            if(resp.data.status=='success'){
                env.object = resp.data.data
                env.bb_yin = env.object.bb_yin
            }else{
                env.object = {}
                env.$toast.error('');
            }
        })
    },

    update(e){
        var env = this
        this.active_yi = -1
        this.$http.put('/open/zi/'+this.object.idx, {'bb_yin': env.bb_yin}).then(function(resp){
            if(resp.data.status=='success'){
                env.object = resp.data.data
                env.bb_yin = env.object.bb_yin
                env.$toast.success('');
            }else{
                env.object = {}
                env.$toast.error('');
            }
        })
    },

    route(path){
      this.$router.push({path:path})
    },
    load_yi(val){
      var env = this
      if(val!=-1 && env.ziyi!=env.zi){
        this.$http.post('/open/zi/'+this.zi).then(function(resp){
          if(resp.data.status=='success'){
                env.yi_list = resp.data.data
                env.ziyi = env.zi
            }else{
                env.$toast.warning('');
            }
        })
      }
    },
  },
  // // before render
  // created:function() {
  //   var env = this
  //   //console.log(this.$refs)
  // },
  // after render
  mounted: function(){
    var env = this
    this.zi = this.$route.params.zi
    if(this.zi){
        this.$http.get('/open/zi/'+this.zi).then(function(resp){
            if(resp.data.status=='success'){
                env.object = resp.data.data
                env.zi = env.object.jian
                env.bb_yin = env.object.bb_yin
            }else{
                env.object = {}
                env.$toast.error('');
            }
        })
    }
  },
}
</script>
