<template>
<mu-container>
    <mu-breadcrumbs>
        <mu-breadcrumbs-item key="" :disabled="false" class="red-font"></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="" :disabled="false" v-if="object.idx">
            <router-link :to="{ name: 'Yunzi', params:{yun:object.gyun} }">{{object.gyun}}</router-link>
        </mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="" :disabled="true" v-if="!object.idx"></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="" :disabled="true" v-if="object.idx">{{object.zi}}{{object.jian}}</mu-breadcrumbs-item>
    </mu-breadcrumbs>
    <mu-row>
        <mu-col span="9">
            <!--
            <mu-text-field v-model="zi" ref="zi" placeholder="" style="width:100%;" @keyup.enter="search"></mu-text-field>
            -->
            <input type="text" v-model="zi" ref="zi" placeholder="" class="my-input" @keyup.enter="search" id="search" @focus="focusInput"/>
        </mu-col>
        <mu-col span="3">
            <mu-button color="primary" @click="search"  class="btn-new">
            <mu-icon value="search"></mu-icon></mu-button>
        </mu-col>
    </mu-row>

    <mu-card style="width: 100%; margin: 0 auto;" v-if="object.idx">
        <!--
      <mu-card-header title="Myron Avatar" sub-title="sub title">
        <mu-avatar slot="avatar">
          <img src="../../assets/images/uicon.jpg">
        </mu-avatar>
      </mu-card-header>
        -->
      <mu-card-title :title="object.zi" :sub-title="': '+object.jian + ' /  : '+object.idx"></mu-card-title>
      <mu-card-actions>
        <mu-button color="primary" flat @click="route('/yun/'+object.gyun)" >: <b>{{object.gyun}}</b></mu-button>
        <mu-button flat>: <b>{{object.fan2}}</b></mu-button>
        <mu-button flat>: <b>{{object.gyxu}}</b></mu-button>
        <mu-button flat>: <b>{{object.diao2 || object.diao}}</b></mu-button>
        <mu-button flat>: <b>{{object.sheng || 'X'}}</b> / <b>{{object.xing||'X'}}</b> / <b>{{object.pang ||
         'X'}}</b></mu-button>

        <mu-button flat>: <b>{{object.she}}</b></mu-button> 
        <mu-button flat>: <b>{{object.hu}}</b></mu-button> 
        <mu-button flat>: <b>{{object.poem}}</b></mu-button> 
        <mu-button flat>(polyhedron): <b>{{object.poly}}</b></mu-button>
        <mu-button flat>: <b>{{object.py}}</b></mu-button>

        <span v-if="object.ps_yun_list"><br/>
        <mu-button color="primary" flat v-for="yun in object.ps_yun_list" :key="yun" @click="route('/psy/'+yun)">
          : <b>{{yun}}</b>
        </mu-button>
        </span>

        <span v-if="object.cz_buyun_list"><br/>
        <mu-button color="primary" flat v-for="yun in object.cz_buyun_list" :key="yun[1]" @click="route('/czy/'+yun[0]+'/'+yun[1])">
          : <b>{{yun[0]}} > {{yun[1]}}</b>
        </mu-button>
        </span>
      </mu-card-actions>
      <mu-card-text>
        <mu-tabs :value.sync="active_yi" inverse color="secondary" text-color="rgba(0, 0, 0, .54)" center @change="load_yi">
          <mu-tab></mu-tab>
          <mu-tab></mu-tab>
          <mu-tab></mu-tab>
          <mu-tab></mu-tab>
        </mu-tabs>
        <!--
        <div class="demo-text" v-if="active_yi === 0">
          <p>{{ ': ' + (object.yi || 'X')}}</p>
        </div>
        -->
        <div class="demo-text" v-if="active_yi === 0">
          <p>{{ ': ' + (yi_list[6]||'') }}</p>
        </div>
        <div class="demo-text" v-if="active_yi === 1">
          <div v-html="yi_list[0]"></div>
        </div>
        <div class="demo-text" v-if="active_yi === 2">
          <div v-html="yi_list[1]"></div>
        </div>
        <div class="demo-text" v-if="active_yi === 3">
          <div v-html="yi_list[2]"></div>
        </div>

      </mu-card-text>
    </mu-card>

    <mu-card v-if="!object.idx">
        <p class="index-card">         601
        </p>
    </mu-card>
</mu-container>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      active_yi: -1,
      title: '',
      zi: '',
      object: {},
      yi_list:['', '', ''],
      ziyi: false,
    }
  },
  methods:{
    focusInput(){
       
    },
    search(e){
        var env = this
        this.$router.push({ name: 'Search', params:{zi: this.zi}})
    },
    route(path){
      this.$router.push({path:path})
    },
    load_yi(val){
      var env = this
      if(val!=-1 && env.ziyi!=env.zi){
        this.$http.post('/open/zi/'+this.zi).then(function(resp){
          if(resp.data.status=='success'){
                env.yi_list = resp.data.data
                env.ziyi = env.zi
            }else{
                env.$toast.warning('');
            }
        })
      }
    },
  },
  // // before render
  // created:function() {
  //   var env = this
  //   //console.log(this.$refs)
  // },
  // after render
  mounted: function(){
    var env = this
    this.zi = this.$route.params.zi
    if(this.$store.zi_dict===undefined){
      this.$store.zi_dict = {}
    }
    if(this.zi){
        var object = env.$store.zi_dict[env.zi]
        if(!(object === undefined)){
          env.object = object
          return
        }
        this.$http.get('/open/zi/'+this.zi).then(function(resp){
            if(resp.data.status=='success'){
                env.object = resp.data.data
                env.$store.zi_dict[env.zi] = env.object
                env.zi = env.object.jian
            }else{
                env.object = {}
                env.$toast.error('');
            }
        })
    }
  },
}
</script>
<style scoped>
.container{padding: 0px 10px;}
</style>